.carousel {
    position: relative;

    &::after {
        content: '';
        @extend %ab-fill;
        background-color: transparent;
    }

    // transition
    &:hover &__control {
        &__arrow {
            opacity: 1;
        }
    }
    
    &-container {
        position: relative;
        height: 450px;
        overflow: hidden;
        background-color: #0b0d12;
    }


    // carousel items
    &-pane {
        // item-container
        @extend %ab-fill;
        z-index: 0;
    }

    // item
    &__item {
        @extend %ab-fill;
        z-index: 0;


        // item bk container
        &__bk-container {
            @extend %ab-fill;
            z-index: 0;

            &.next-in,&.prev-in {
                animation-duration: 5s;
                animation-timing-function: ease-out;
                animation-fill-mode: backwards;
            }
            &.next-in {
                animation-name: slide-left;
            }

            &.prev-in {
                animation-name: slide-right;
            }
        }
        

        // item bk
        &__bk {
            @extend %ab-fill;
            background-repeat: no-repeat;


            &.mobile {
                background-size: contain;
                display: block;
            }
            &.desk {
                display: none;
            }
        }

        // item content
        &__content {
            position: absolute;
            left: 0px; right: 0;
            
            &-group {
            }

            &__logo {
                animation-delay: .5s;
            }

            &__title {
                @include font(6,0,3);
                letter-spacing: 1px;
                margin-bottom: nth($m,6);
                margin-top: nth($m,6);
                line-height: 1.2;

                animation-delay: .7s;
                
                > a {
                    text-decoration: none;
                    color: $color-white;
                }
            }

            &__btn {
                @include btn-base();
                animation-delay: .9s;
            }

            &__logo,&__title,&__btn {
                &.next-in,&.prev-in {
                    animation-duration: 5s;
                    animation-timing-function: ease-out;
                    animation-fill-mode: backwards;
                }
                &.next-in {
                    animation-name: slide-left;
                }
    
                &.prev-in {
                    animation-name: slide-right;
                }
            }
        }

    }


    // controlers
    &__control {
        @extend %ab-fill;
        z-index: 1;
        

        // points
        &__points {
            position: absolute;
            left: 0; right: 0;
            bottom: 30px;
            font-size: 0;
            text-align: center;

            > li {
                display: inline-block;
                width: 12px;
                height: 12px;
                background-color: #fff;
                border-radius: 50%;
                margin-left: 6px;
                margin-right: 6px;
                cursor: pointer;
                
                &.active {
                    background-color: #E8BE02;
                }
            }
        }

        // arrow 
        &__arrow {
            position: absolute;
            top: 0; bottom: 0;
            width: 36px;
            height: 60px;
            margin-top: auto;
            margin-bottom: auto;
            cursor: pointer;
            z-index: 1;
            background-color: rgba($color-black,.3);

            // transition
            opacity: 0;
            transition: opacity .3s;

            > span {
                position: absolute;
                top: 0; bottom: 0;
                margin-top: auto;
                margin-bottom: auto;
                width: 15px;
                height: 15px;
                cursor: pointer;
    
                &::after {
                    content: '';
                    position: absolute;
                    left: 0; right: 0; top: 0; bottom: 0;
                    margin: auto;
                    border-width: 0;
                    border-style: solid;
                    border-top-width: 3px;
                    color: #4296ce;
                }
            }

            &.next {
                right: 15px;
                
                > span {
                    left: 6.9px;
                    transform: rotateZ(45deg);
                    &::after {
                        border-right-width: 3px;
                    }
                }
            }

            &.prev {
                left: 15px;

                > span {
                    right: 6.9px;
                    transform: rotateZ(-45deg);
                    &::after {
                    border-left-width: 3px;
                        
                    }
                }
            }

            &:hover {
                > span {
                    &::after {
                        color: #90e4ff;
                    }
                }
            }
        }

        
        // timer bar
        &__timer {
            position: absolute;
            left: 0; right: 0;
            bottom: 0;
            background-color: $timerContainerBk;

            &-list {
                display: flex;
                justify-content: center;
                align-items: center;

                > li {
                    position: relative;
                    flex-basis: 25%;
                    height: $timerMobileHeight;
                    margin-left: nth($m,1);
                    margin-right: nth($m,1);

                    // transition
                    transition: transform .6s;

                    // when timer running
                    &.active {
                        transform: scaleY(1.75);

                        &::before {
                            background-color: $timerActiveBk;
                        }

                        > .bar {
                            width: 100%;
                            visibility: visible;
                        }
                    }

                    &::before {
                        content: '';
                        position: absolute;
                        left: 0; right: 0;
                        top: $timerBarHeight; bottom: $timerBarHeight;
                        background-color: $timerBaseBk;
                    }

                    > .bar {
                        position: absolute;
                        left: 0;

                        // setting width to run timer bar
                        width: 0;
                        visibility: hidden;

                        top: $timerBarHeight; bottom: $timerBarHeight;
                        background-color: $timerActive;
                        overflow: hidden;

                        // transition 
                        transition: width $timerRunTime linear;
                    }
                }
            }
        }
    }


    @include respond(sm) {
        &__item {
            
            &__bk {
    
                &.mobile { }

                &.desk { }
                
            }
    
            &__content {

                &__title { }

                &__btn {
                    @include link { }
                }
            }
        }

        &__control {
            &__timer {
                &-list {
                    > li {
                        flex-basis: 60px;
                        height: $timerDeskHeight;

                        &::before , & > .bar {
                            top: $timerDeskBarHeight; bottom: $timerDeskBarHeight;
                        }

                    }
                }
            }
        }
    }
}

/* animation */

.slide-left-enter-active {
    animation-name: slide-left;
    animation-duration: 5.5s;
    animation-timing-function: ease-out;

    > .carousel__item__content {
        &__logo {
            animation-delay: .5s;
        }
    }
}


.opacity-enter {
    opacity: 0;
}
.opacity-leave,.opacity-enter-to{
    opacity: 1;
}
.opacity-leave-to {
    opacity: .3;
}
.opacity-leave-acitve {
    transition: opacity .42s;
}



@keyframes slide-left {
    0% {
        transform: translateX(120px);
        opacity: 0;
    }

    9% {
        transform: translateX(20px);
        opacity: 1;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slide-right {
    0% {
        transform: translateX(-120px);
        opacity: 0;
    }

    9% {
        transform: translateX(-20px);
        opacity: 1;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}


